<template>
  <v-container>
    <h1>Aldeanos</h1>
    <v-row>
      <v-col cols="6">
        <v-text-field
          v-model="id_villager"
          label="Buscar aldeano por ID"
          clearable
        ></v-text-field>
      </v-col>
      <v-col align-self="center">
        <v-btn class="ma-2" :loading="loading" elevation="2" @click="getVillager"> Buscar </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-card v-if="villager.name!=''" class="mx-auto" max-width="344">
          <v-img
            :src="villager.image"
            height="200px"
          ></v-img>

          <v-card-title> Nombre: {{villager.name}}</v-card-title>

         <v-card-text>
             <div class="text-subtitle-1"> Genero:{{genero}}</div>
             <div class="text-subtitle-1"> Personalidad:{{villager.personality}}</div>
         </v-card-text>

        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Animal-component",
  // props: {},
  data: function () {
    return {
        id_villager: "",
        loading:false,
        villager:{
            name:'',
            image:'',
            gender:'',
            personality:'',
        }
        
    };
  },
  computed: {
      genero(){
          if(this.villager.gender ==="Male"){
              return "Masculino"
          }
          else if(this.villager.gender ==="Female"){
              return "Femenino"
          }
          return 'Desconocido'
      }
  },
  methods: {
    async getVillager(){
        try {
            this.loading =true
            let response = await fetch(`https://acnhapi.com/v1/villagers/${this.id_villager}`)
            let data = await response.json()
            this.loading = false
            console.log(data);
            this.setVillager(data)
        } catch (error) {
            console.log(error)
        }
    },
    setVillager(villager){
        this.villager.name = villager.name['name-EUes'] 
        this.villager.image = villager.image_uri
        this.villager.gender = villager.gender
        this.villager.personality = villager.personality
    }
  },
  // watch: {},
  // components: {},
  // mixins: [],
  // filters: {},
  // -- Lifecycle Methods
  // -- End Lifecycle Methods
};
</script>

